.App {
  text-align: center;
  /*background-color: ##E6E7E8;*/
  zoom: 0.9;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.card-navbar {
box-shadow: black;
/*background-color: #0074D9;*/
height: 75px;
margin: 15px;
zwidth: 80%;
display: inline-block;
}

.hashtag{
  text-align:"left";
  color:darkgray;
  display: table-cell;
  
}

.translate {
  width: "15px";
  height: "15px";
  display: inline;
}

.App-link {
  color: #61dafb;
}
input.searchbar {
  text-align: center;
  background-color: white;
  background-image: url("images/search-icon.png");
  background-size: 18px;
  background-repeat: no-repeat;
  background-position: 7px 7px;
  ;
  width:75% ;
  height: 25px;
  /*border-bottom-left-radius: 8px;*/
  border-radius: 15px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  
}
input:focus {
  outline:none;
}

tr{
  background-color: black;
  color:white;
  opacity: 0.7;
  border-radius: 15px;
}
table{
  width:-webkit-fill-available;
  opacity: 1;
  position:absolute;
  border-radius: 15px;

  border-collapse: separate;
}

td { border: solid 1px #000; 
border-radius: 15px;
}
/*tr:first-child td:first-child { border-top-left-radius: 10px; }
tr:first-child td:last-child { border-top-right-radius: 10px; }
tr:last-child td:first-child { border-bottom-left-radius: 10px; }
tr:last-child td:last-child { border-bottom-right-radius: 10px; }
*/

tr:hover td{
  color: red;
  cursor:pointer;
}

.cardComponent{
  margin-top: 25px;
}

.cardExample{
  width: 275px;
  height: 320px;
  /*width: 100%;
  height: 100%;*/
  display :inline-block;
  margin:20px;
  border: 1px solid #ccc;
  border-radius: 20px;
  margin: 10px 5px;
  padding: 4px;
  
}

.cardExample:hover{
  
    box-shadow: 0 10px 20px rgba(255,0,0,0.54),0 6px 6px rgba(224, 94, 94, 0.23);
    backgroundColor:red;
    cursor: pointer;
  
}

.world{
  width: 85%;
  height: 160px;
  /*width: 100%;
  height: 100%;*/
  display :inline-block;
  margin:20px;
  border: 1px solid #ccc;
  /*border-radius: 20px;*/
  margin: 10px 5px;
  padding: 4px;
  /*align-content: center;*/
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  
}

.globalRow{
  width: 85%;
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}


.clock{
display: block;
text-align: right;
}

.popup {
  position: fixed;
  /*width: 100%;
  height: 100%;*/
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.5);
  display: inline-block;
  align-items: center;
  

}

.popup-content{

  position: relative;
  width:  600px;
  height: 400px;
  align-items: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.5);
  filter:blur(2000px);
  vertical-align: middle  ;

}

.outer{
  display: table;
  position: fixed;
  top: 0;
  left: 0;
  bottom:0;
  right:0;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0, 0.5); 
}

.middle{
  display: table-cell;
  vertical-align: middle;
}

.inner{
  margin-left: auto;
  margin-right: auto;
  width: 600px;
  color: white;
}
.innerMap{
  margin-left: auto;
  margin-right: auto;
  width: 600px;
  align-items: center

}

#mapX{
  width: 600px;
  height: 400px;
  display: inline-block;
  position: relative;
}


#mapY{
  position: relative;
  width: 600px;
  height: 400px;
  align-items: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
